/* eslint-disable no-unused-vars */
import store from './store';
function getPositionAtCenter(element) {
    const { top, left, width, height, } = element.getBoundingClientRect();
    return {
        x: left + width / 2,
        y: top + height / 2,
    };
}
export function getDistanceBetweenElements(a, b) {
    const aPosition = getPositionAtCenter(a);
    const bPosition = getPositionAtCenter(b);
    return Math.hypot(aPosition.x - bPosition.x, aPosition.y - bPosition.y);
}
export function animation(car, distance, animationTime) {
    let start = 0;
    const state = {};
    function step(timestamp) {
        if (!start) {
            start = timestamp;
        }
        const time = timestamp - start;
        const passed = Math.round(time * (distance / animationTime));
        const catStyle = car;
        catStyle.style.transform = `translateX(${Math.min(passed, distance)}px)`;
        if (passed < distance) {
            state.id = window.requestAnimationFrame(step);
        }
    }
    state.id = window.requestAnimationFrame(step);
    return state;
}
const models = ['Audi', 'BMW', 'Ford', 'Honda', 'Hyundai', 'Kia', 'Lada', 'Mazda', 'Toyota', 'Subaru'];
const names = ['A6', 'Cabriolet', 'Sport', 'TT', 'Caprice', 'Daster', 'Matiz', 'Tornado', 'Volt', 'EV8'];
const getRandomName = () => {
    const model = models[Math.floor(Math.random() * models.length)];
    const name = names[Math.floor(Math.random() * models.length)];
    return `${model} ${name}`;
};
const getRandomColor = () => {
    const letters = '0123456789ABCDEF';
    let color = '#';
    for (let i = 0; i < 6; i += 1) {
        color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
};
export const generateRandomCars = (count = 100) => new Array(count).fill(1).map(() => ({ name: getRandomName(), color: getRandomColor() }));
export const raceAll = async (promises, ids) => {
    const { success, id, time } = await Promise.race(promises);
    if (!success) {
        const failedIndex = ids.findIndex((i) => i === id);
        const restPromises = [
            ...promises.slice(0, failedIndex), ...promises.slice(failedIndex + 1, promises.length)
        ];
        const restIds = [...ids.slice(0, failedIndex), ...ids.slice(failedIndex + 1, ids.length)];
        return raceAll(restPromises, restIds);
    }
    return Object.assign(Object.assign({}, store.cars.find((car) => car.id === id)), { time: +(time / 1000).toFixed(2) });
};
export const race = async (action, isRace) => {
    const promises = store.cars.map(({ id }) => action(id, isRace));
    const winner = await raceAll(promises, store.cars.map((car) => car.id));
    return winner;
};
export function getSort(sort) {
    if (store.sortBy === sort) {
        if (store.sortOrder === 'DESC') {
            return '‚ñº';
        }
        return '‚ñ≤';
    }
    return '';
}
export function getDisabled(array) {
    array.forEach((e) => {
        const elem = e;
        if (elem.disabled === true) {
            elem.disabled = false;
        }
        else {
            elem.disabled = true;
        }
        return elem;
    });
    return array;
}
