import Page from '../../templates/pages';
import store from '../../../scripts/store';
import UI from '../../../scripts/UI';
import { getSort } from '../../../scripts/utils';
class WinnersPage extends Page {
    constructor() {
        super(...arguments);
        this.nextPage = false;
        this.prevPage = true;
        this.maxPages = Math.ceil(Number(store.winnersCount) / 10);
    }
    render() {
        if (store.winnersPage >= this.maxPages) {
            this.nextPage = true;
        }
        const title = WinnersPage.TextObject.WinnersTitle;
        this.container.innerHTML = `
    <h1>${title} (${store.winnersCount})</h1>
    <div class='pagination'>
      <button class='winners-page-prev button' ${this.prevPage ? 'disabled = \'true\'' : ''} id='winners-prev'>‚ùÆ</button>
      <div>Page: ${store.winnersPage}</div>
      <button class='winners-page-next button' ${this.nextPage ? 'disabled = \'true\'' : ''} id='winners-next'>‚ùØ</button>
    </div>
    <table class='table'>
      <tr class='table-row head-row'>
        <th class='table-col-head' id='sort-by-id'>‚Ññ</th>
        <th class='table-col-head'>Car</th>
        <th class='table-col-head'>Name</th>
        <th class='table-col-head col-wins' id='sort-by-wins'>
          Wins 
          ${getSort('wins')}
        </th>
        <th class='table-col-head col-time' id='sort-by-time'>
          Best time(s) 
          ${getSort('time')}
        </th>
      </tr>
      <tbody>
      ${store.winners.map((winner, index) => `<tr class='table-row'>
        <td class='table-col'>${index + 1 + ((store.winnersPage - 1) * 10)}</td>
        <td class='table-col col-img'>${UI.renderCarImage(winner.car.color)}</td>
        <td class='table-col'>${winner.car.name}</td>
        <td class='table-col'>${winner.wins}</td>
        <td class='table-col'>${winner.time}</td>
      </tr>`).join('')}
      </tbody>
    </table>
    `;
        return this.container;
    }
    listen(container) {
        container.addEventListener('click', async (e) => {
            // const parent = (<HTMLElement>e.target).parentNode;
            const element = e.target;
            if (element.closest('.winners-page-next')) {
                if (store.winnersPage < this.maxPages) {
                    this.prevPage = false;
                    store.winnersPage += 1;
                    if (store.winnersPage === this.maxPages) {
                        this.nextPage = true;
                    }
                    await UI.updateStateWinners();
                    this.render();
                }
            }
            if (element.closest('.winners-page-prev')) {
                if (store.winnersPage > 1) {
                    this.nextPage = false;
                    store.winnersPage -= 1;
                    if (store.winnersPage === 1) {
                        this.prevPage = true;
                    }
                    await UI.updateStateWinners();
                    this.render();
                }
            }
            if (element.id === 'sort-by-wins') {
                store.sortBy = 'wins';
                store.sortOrder = (store.sortOrder === 'DESC') ? 'ASC' : 'DESC';
                await UI.updateStateWinners();
                this.render();
            }
            if (element.id === 'sort-by-time') {
                store.sortBy = 'time';
                store.sortOrder = (store.sortOrder === 'DESC') ? 'ASC' : 'DESC';
                await UI.updateStateWinners();
                this.render();
            }
        });
    }
}
WinnersPage.TextObject = {
    WinnersTitle: 'Winners',
};
export default WinnersPage;
