import { getCars, getWinners } from './api';
const { items: cars, count: carsCount } = await getCars(1);
const { items: winners, count: winnersCount } = await getWinners({
    page: 1, limit: 10, sort: null, order: null,
});
export default {
    carsPage: 1,
    cars,
    carsCount,
    animation: {},
    winnersPage: 1,
    winners,
    winnersCount,
    sortBy: 'id',
    sortOrder: 'DESC',
    view: 'garage',
};
